.banner {
  color: white;
  object-fit: contain;
  height: 56.25vw;
  min-height: 448px;
  max-height: 800px;
  position: relative;
  background: linear-gradient(
    180deg,
    transparent 0%,
    rgba(0, 0, 0, 0.3) 50%,
    #111 100%
  );
}

.banner_contents {
  margin-left: 4%;
  padding-top: 140px;
  height: 190px;
  max-width: 600px;
  z-index: 2;
  position: relative;
}

.banner_title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  line-height: 1.1;
}

.banner_description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 1rem;
  font-size: 1rem;
  max-width: 360px;
  height: 180px;
}

.banner_buttons {
  display: flex;
  gap: 10px;
}

.banner_button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 4px;
  padding: 0.5rem 2rem;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.8);
  transition: all 0.3s ease;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.banner_button:hover {
  color: #000;
  background-color: #e6e6e6;
  transform: scale(1.05);
  transition: all 0.3s ease;
}

.banner_button.play {
  background-color: #e50914;
}

.banner_button.play:hover {
  background-color: #f40612;
  color: #fff;
}

.banner--fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
  position: absolute;
  bottom: 0;
  width: 100%;
}
