.footer_container {
  background-color: #141414;
  color: #999;
  padding: 70px 4% 30px;
  font-size: 13px;
  border-top: 1px solid #333;
  margin-top: 50px;
}

.footer_socials {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
}

.footer_socials svg {
  font-size: 24px;
  color: #999;
  cursor: pointer;
  transition: all 0.3s ease;
}

.footer_socials svg:hover {
  color: #e50914;
  transform: scale(1.1);
}

.footer_links {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.footer_links ul {
  list-style: none;
  padding: 0;
}

.footer_links li {
  margin: 8px 0;
  cursor: pointer;
  transition: color 0.3s ease;
}

.footer_links li:hover {
  color: #e50914;
  text-decoration: underline;
}

.footer_code button {
  background: transparent;
  border: 1px solid #666;
  color: #999;
  padding: 8px 16px;
  font-size: 12px;
  cursor: pointer;
  margin-bottom: 20px;
  transition: all 0.3s ease;
  border-radius: 4px;
}

.footer_code button:hover {
  border-color: #e50914;
  color: #e50914;
  background-color: rgba(229, 9, 20, 0.1);
}

.footer_copy {
  font-size: 11px;
  color: #666;
  text-align: center;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #333;
}
