/* Netflix-style Row Container */
.row {
  color: white;
  margin-left: 20px;
  margin-bottom: 30px;
  position: relative;
}

/* Netflix-style Row Title */
.row h2 {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 15px;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

/* Netflix-style Posters Container */
.row_posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px 0;
  gap: 10px;
  scroll-behavior: smooth;
}

/* Hide scrollbar but keep functionality */
.row_posters::-webkit-scrollbar {
  display: none;
}

.row_posters {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

/* Netflix-style Individual Poster */
.row_poster {
  width: 100%;
  max-height: 100px;
  object-fit: contain;
  margin-right: 10px;
  transition: transform 450ms;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  position: relative;
}

/* Netflix-style Large Poster (Netflix Originals) */
.row_posterLarge {
  max-height: 250px;
  border-radius: 6px;
}

/* Netflix-style Hover Effects */
.row_poster:hover {
  transform: scale(1.08);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
  z-index: 10;
}

.row_posterLarge:hover {
  transform: scale(1.09);
}

/* Netflix-style Movie Title Overlay */
.row_poster::after {
  content: attr(data-title);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
  color: white;
  padding: 20px 10px 10px;
  font-size: 0.8rem;
  font-weight: 600;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 0 0 4px 4px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
}

.row_poster:hover::after {
  opacity: 1;
}

/* Netflix-style Row Fade Effects */
.row::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 100%;
  background: linear-gradient(90deg, transparent, #111);
  pointer-events: none;
  z-index: 5;
}

/* Netflix-style YouTube Trailer Container */
.row > div:last-child {
  margin-top: 20px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

/* Netflix-style Responsive Design */
@media (max-width: 768px) {
  .row {
    margin-left: 10px;
  }

  .row h2 {
    font-size: 1.2rem;
  }

  .row_poster {
    max-height: 80px;
  }

  .row_posterLarge {
    max-height: 200px;
  }

  .row_posters {
    gap: 5px;
  }
}

@media (max-width: 480px) {
  .row {
    margin-left: 5px;
  }

  .row h2 {
    font-size: 1rem;
  }

  .row_poster {
    max-height: 60px;
  }

  .row_posterLarge {
    max-height: 150px;
  }
}

/* Netflix-style Loading Animation */
.row_posters.loading {
  opacity: 0.5;
}

.row_posters.loading::after {
  content: "Loading...";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #e50914;
  font-size: 1.2rem;
  font-weight: 600;
}

/* Netflix-style Special Effects */
.row_poster::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    45deg,
    transparent 30%,
    rgba(229, 9, 20, 0.1) 50%,
    transparent 70%
  );
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 4px;
}

.row_poster:hover::before {
  opacity: 1;
}
