/* Netflix-style Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #111;
  font-family: "Helvetica Neue", Arial, sans-serif;
  color: #fff;
  line-height: 1.4;
  overflow-x: hidden;
}

/* Netflix-style typography */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  color: #fff;
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
}

h2 {
  font-size: 1.5rem;
  font-weight: 600;
}

/* Netflix-style links */
a {
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #e50914;
}

/* Netflix-style buttons */
button {
  background: transparent;
  border: none;
  color: #fff;
  cursor: pointer;
  font-family: inherit;
  transition: all 0.3s ease;
}

button:hover {
  color: #e50914;
}

/* Netflix-style images */
img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Netflix-style smooth scrolling */
html {
  scroll-behavior: smooth;
}
