.header_outer_container {
  width: 100%;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.7) 0%,
    rgba(0, 0, 0, 0.4) 50%,
    transparent 100%
  );
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  transition: background 0.3s ease;
}

.header_outer_container.scrolled {
  background: #141414;
}

.header_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 4%;
  height: 68px;
  max-width: 1920px;
  margin: 0 auto;
}

.header_left ul,
.header_right ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.header_left li,
.header_right li {
  margin: 0 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: color 0.3s ease;
}

.header_left li:hover {
  color: #e50914;
}

/* Netflix left menu text */
.header_left li {
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.header_left img {
  margin-right: 10px;
}

/* ICONS STYLE */
.header_right li svg {
  font-size: 28px; /* icon size */
  color: white;
  transition: color 0.3s ease;
}

.header_right li svg:hover {
  color: #e50914; /* Netflix red hover */
}
